/* Variables */
:root {
  --transition-short: 0.1s;
  --transition-default: 0.3s;
  --transition-extended: 0.6s;

  --material-shadow0: rgba(0, 0, 0, 0.12) 0px 1px 3px,
    rgba(0, 0, 0, 0.24) 0px 1px 2px;
  --material-shadow1: rgba(0, 0, 0, 0.16) 0px 3px 6px,
    rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

body.square {
  --border-radius: .5rem;
}
body.squareFull {
  --border-radius: 0;
}
body.circle {
  --border-radius: 2rem;
}

body.mocha {
  /* Mocha */
  --rosewater: #f5e0dc;
  --flamingo: #f2cdcd;
  --pink: #f5c2e7;
  --mauve: #cba6f7;
  --red: #f38ba8;
  --maroon: #eba0ac;
  --peach: #fab387;
  --yellow: #f9e2af;
  --green: #a6e3a1;
  --teal: #94e2d5;
  --sky: #89dceb;
  --sapphire: #74c7ec;
  --blue: #89b4fa;
  --lavender: #b4befe;
  --text: #cdd6f4;
  --subtext1: #bac2de;
  --subtext0: #a6adc8;
  --overlay2: #9399b2;
  --overlay1: #7f849c;
  --overlay0: #6c7086;
  --surface2: #585b70;
  --surface1: #45475a;
  --surface0: #313244;
  --base: #1e1e2e;
  --mantle: #181825;
  --crust: #11111b;

  --color-block-overlay: rgba(0, 0, 0, 0.7);
  --shadow-light: rgba(
    45,
    35,
    66,
    0.4
  ); /* Slightly darker shadow for the dark theme */
  --shadow-medium: rgba(45, 35, 66, 0.3);
  --shadow-inset: var(--surface2);
}

